/* RESET */
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* FLEX */

.flex {
  display: -ms-flexbox;
  display: flex;
}

.flex-1{
  -ms-flex: 1 1;
      flex: 1 1;
}

.flex-wrap{
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.vertical {
  -ms-flex-direction: column;
      flex-direction: column;
}

.flex-center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

/* COMPONENTS */

.App {
  width: 100%;
  height: 100vh;
}

.card {
  border: 1px solid #777;
  padding: 10px;
  margin: 10px;
  width: 200px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.card > hr {
  border: 1px solid #222;
  width: 100%;
}


